.journal__main-content {
  display: flex;
}
.journal__sidebar {
  background-color: $dark-grey;
  color: white;
  display: flex;
  flex-direction: column;
  height: 100vh; //vh = viewHeight viewWidth vw
  padding-left: 10px;
  padding-right: 10px;
  width: 450px;
}

.journal_sidebar-navbar {
  display: flex;
  justify-content: space-between;
  h3 {
    font-weight: lighter;
  }
}

.journal__new-entry {
  align-items: center;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 30px;
  width: 100%;

  transition: color 0.3s ease;
  &:hover {
    color: darken($color: white, $amount: 20);
  }
}
.journal__entries {
  flex: 1 1 auto;
  overflow: scroll;
  margin-top: 30px;
}
//Oculta la barra de scroll
.journal__entries::-webkit-scrollbar {
  display: none;
}
.journal__entry {
  background-color: white;
  border-radius: 4px;
  color: $dark-grey;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  overflow: hidden;
}
.journal__entry-picture {
  height: 75px;
  width: 75px;
}
.journal__entry-body {
  padding: 10px;
}
.journal_entry-title {
  font-size: 14px;
  font-weight: bold;
}
.journal_entry-content {
  font-size: 10px;
}
.journal_entry-date-box {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px;
  span {
    font-size: 12px;
  }
}
